import React, { PureComponent } from 'react';
import styles from './SendChanges.module.scss';

import { inject, observer } from 'mobx-react';

import ActionButton from './ActionButton';

import { GraphStoreType } from '../../../stores/graph/graphStore';
import Icon from '../../Icon';
import { CommonStoreType } from '../../../stores/common/commonStore';

type SendChangesProps = {
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  [key: string]: any;
};

type SendChangesState = {
  graphID: any;
};

/**
 * Компонент модального окна по передаче изменений
 */
@inject('graphStore', 'commonStore')
@observer
class SendChanges extends PureComponent<SendChangesProps, SendChangesState> {
  constructor(props: SendChangesProps) {
    super(props);

    const { graphStore } = props;

    const graphID = graphStore?.previouslySchemeId;

    this.state = {
      graphID: graphID,
    };
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { onClose, commonStore } = this.props;

    return (
      <div className={styles.container}>
        <div className={styles.header} data-draggable>
          <span className={styles.header_text} data-draggable>
            Передать изменения
          </span>
          <Icon
            className={styles.close_icon}
            name="close"
            onClick={onClose}
            theme={commonStore?.appTheme}
          />
        </div>
        <div className={styles.data_container}>Изменения по схеме переданы в Досье проверки</div>
        <div className={styles.data_button_container}>
          <div className={styles.data_action_button}>
            <ActionButton onClick={onClose} isSecondary={true} label="Закрыть" />
          </div>
        </div>
      </div>
    );
  }
}

export default SendChanges;
